.coin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-height: 250px;
  min-width: 270px;
  color: white;
  background-color: rgba(0, 0, 0, 0.9);
  border-radius: 15px;
  margin: 20px;
}
img {
  height: 50px;
  width: 50;
  object-fit: contain;
}
h2,
h3,
img {
  background-color: #010204;
}
